import './App.css';
import React, { useRef,useState,useEffect} from 'react';
import IconButton from '@material-ui/core/IconButton';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import FavoriteIcon from '@material-ui/icons/Favorite';




                        function useOnScreen(ref, rootMargin = '0px') {
                              const [isIntersecting, setIntersecting] = useState(false);
                        
                              useEffect(() => {
                              const observer = new IntersectionObserver(
                              ([entry]) => {
                                    setIntersecting(entry.isIntersecting);
                              },
                              {
                                    rootMargin
                              }
                              );
                              if (ref.current) {
                              observer.observe(ref.current);
                              }
                              return () => {
                              observer.unobserve(ref.current);
                              };
                              }, []); 
    
                              return isIntersecting;
                        }


                  function Video ({source}){
                        const [play, setPlay] = useState(false)
                        const [click, setClicked] = useState(false)
                        const [count, setCount] = useState()
                        const videoRef= useRef(null)
                        const audioRef= useRef(null)
                        const playRef= useRef(false)
                        const ref = useRef()
                        const navSec = useRef(null)

                        const onScreen = useOnScreen(ref, '100px');

                        const clickFunc = () =>{
                              if(click === false){
                                    setClicked(true)
                                    setCount(1)
                              }
                              else{
                                    setClicked(false)
                                    setCount('')
                                    console.log(click)

                              }
                        }
                        useEffect(()=>{
                              if(onScreen === true ){
                                    videoRef.current.play()
                                    audioRef.current.play()
                                    videoRef.current.currentTime=0
                                    audioRef.current.currentTime=0
                                    playRef.current.style.display ='none'

                                    
                              }
                              else{
                                    videoRef.current.pause()
                                    audioRef.current.pause() 
                                    videoRef.current.currentTime=0
                                    audioRef.current.currentTime=0

                              }
                        })
                        const videoPlay = ()=>{
                              if(videoRef.current.paused=== false&&  audioRef.current.paused === false
                              ){
                                    videoRef.current.pause()
                                    audioRef.current.pause()
                                    playRef.current.style.display ='block'
                              }else{
                              videoRef.current.play()
                              audioRef.current.play()   
                              playRef.current.style.display ='none'

                              }
                        }
                        console.log(source.substring(0,32)+'DASH_480.mp4?source=fallback')
                        return(
                              
                        <div id='video-container'  ref={ref}>  
                        {/* <div id='navbar-section'ref={navSec}>

                        <a  onClick={()=>{
                              navSec.current.style.display='none'} } class="close"></a>     

                              <ul id='category-list'>
                              <li className='categories'>
                                faget    
                              </li>      
                                    </ul>          
                                       </div>       */}
                        <img ref={playRef} id='play-btn' src= './play.png'  alt='play'/>
                        
                        <h1  id='logo'> F</h1>
                  
                        {/* <div id='burger-parent' onClick={()=>{
                              navSec.current.style.display='block' 
                              videoRef.current.pause()
                              audioRef.current.pause()
                        } }>
                        <div className='burger-btn'></div>
                        <div className='burger-btn'></div>
                        <div className='burger-btn'></div>
                        </div> */}
                        
                        <div id='ld'>
                        <IconButton id='icons' onClick={clickFunc}>
      {click ? <FavoriteIcon style={{ fontSize: '40px' }} />  :<FavoriteBorderIcon style={{ fontSize: '40px' }}/> }
    </IconButton>
                        <span id='like-count'>{count}</span>
</div>

               {  source ? <video  ref={videoRef} 
                  onClick={videoPlay}
                  width="450" height="900"
                        src={source.substring(0,32)+'DASH_480.mp4?source=fallback'}
                        type="video/mp4"
                        preload='none'
                        ></video>: <div>Loading...</div> } 
                        {source ?<audio ref={audioRef}  >      
                        <source
                          preload='none'

                        src={ source&& source.slice(0,37)+'audio.mp4' }>
                              </source>
                              </audio>: console.log('audio element is loading')}
                        </div>     
                        )


                  }
                  export default Video
